.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.my-pad {
  padding: 0!important;
}

.my-form{
  width: 500px!important;
  .form-control{
    background: #312b48;
    border: 1px solid #231f34;
    box-shadow: 0 0 0 0 rgba(0,0,0,.15);
    padding: 10px;
    color: white;
  }
  .form-control:focus{
    background: #312b48;
    border: 1px solid #1dc778;
    box-shadow: 0 0 0 0 rgba(0,0,0,.15);
    color: white;
  }
  .submit-btn{
    background: #1dc778;
    color: white;
  }
}
.content-to-middle{
  width: 500px;
  height: 450px;

  position:absolute; /*it can be fixed too*/
  left:0; right:0;
  top:0; bottom:0;
  margin:auto;

  /*this to solve "the content will not be cut when the window is smaller than the content": */
  max-width:100%;
  max-height:100%;
  overflow:auto;
}

