.navbar {
    li {
        a {
            // color: white;
            // font-weight: 700;
            // position: relative;
            // padding: 14px 20px;
            // transition: all .3s ease;
            border-radius: 0 0 10px 10px;
            color: white;
            // display: block;
            // float: left;
            font-size: .85rem;
            font-weight: 700;
            letter-spacing: .5px;
            padding: 14px 20px;
            position: relative;
            transition: all .3s ease;
        }
        a:hover {
            color: #ff3a4e;
        }
        .activeLink {
            // background: #ff3a4e;
            z-index: 100;
        }
        .activeLink:hover {
            color: white;
        }
        a:after{
            background: #ff3a4e;
            border-radius: 0 0 10px 10px;
            bottom: 0;
            content: "";
            height: 0%;
            left: 8px;
            position: absolute;
            transition: all .3s ease;
            width: calc(100% - 16px);
            z-index: -1;
        }
        .activeLink:after{
            height: 100%;
        }
    }
}
.dark {
    background: rgba(0,0,0,.5);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 30000;
}

.side-menu {
    background: #231f34;
    display: none;
    height: 100%;
    max-width: 300px;
    position: fixed;
    transform: translate(-100%);
    z-index: 30000;
    transition: all .3s ease;
    width: 100%;
}
.active-side-menu {
transform: translate(0)!important;
}
@media only screen and (max-width: 1160px){
    .side-menu {
        display: block;
    }
}

.side-menu {
    ul {
        list-style: none;

        li {
            padding: 5px 15px;
        }
    }
}

